import {useState, useMemo} from 'react'
import {AuthenticatedTemplate, UnauthenticatedTemplate, MsalProvider} from '@azure/msal-react'
import {PublicClientApplication} from '@azure/msal-browser'
import {msalConfig} from '../auth-config'
import {SignInButton} from '../components/SignInButton'
import {SignOutButton} from '../components/SignOutButton'
import {RegisterVNetForm} from '../components/RegisterVNetForm'
import {BaseStyles, Flash} from '@primer/react'
import {verifiedFetchJSON} from '@github-ui/verified-fetch'
import {useRoutePayload} from '@github-ui/react-core/use-route-payload'
import type {
  AppState,
  ISubscriptionProps,
  IVirtualNetworkProps,
  ISubnetProps,
} from '../types/virtual-network-settings-types'

interface Business {
  readonly name: string
}
interface RoutePayload {
  readonly business: Business
  readonly formAction: string
}

export function NewPage() {
  const payload: RoutePayload = useRoutePayload()
  const msalInstance = useMemo(() => new PublicClientApplication(msalConfig()), [])
  const businessName = payload.business.name
  const {formAction} = payload
  const [error, setError] = useState<Error | null>(null)
  const [appState, setAppState] = useState<AppState>({
    accessToken: null,
    selectedSubscription: null,
    selectedResourceGroup: null,
    selectedVirtualNetwork: null,
    selectedSubnet: null,
    subnets: [],
    subscriptionError: null,
    resourceGroupError: null,
    virtualNetworkError: null,
    subnetError: null,
  })

  const createNetworkRegistration = async (
    subscription: ISubscriptionProps,
    vnet: IVirtualNetworkProps,
    subnet: ISubnetProps,
  ) => {
    const resp = await verifiedFetchJSON(formAction, {
      method: 'POST',
      body: {
        subnet_id: subnet.id,
        subnet_name: subnet.name,
        subscription_id: subscription.id,
        subscription_name: subscription.name,
        virtual_network_name: vnet.name,
      },
    })

    if (resp.ok) {
      const body = await resp.json()
      window.location.href = body.redirect_url
      return
    }

    const body = await resp.json()
    if (body.errors.length > 0) {
      setError(Error(body.errors[0]))
      return
    }

    throw Error('Unknown error')
  }

  return (
    <MsalProvider instance={msalInstance}>
      <BaseStyles>
        <div className="Subhead">
          <h2 className="Subhead-heading">
            Add your Azure virtual network to <span className="text-bold">{businessName}</span>
          </h2>
          <div className="Subhead-actions">
            <AuthenticatedTemplate>
              <SignOutButton />
            </AuthenticatedTemplate>
          </div>
        </div>

        <div className="App">
          <AuthenticatedTemplate>
            {error && <Flash variant="danger">{error.message}</Flash>}
            <RegisterVNetForm
              appState={appState}
              setAppState={setAppState}
              onError={setError}
              businessName={businessName}
              createNetworkRegistration={createNetworkRegistration}
            />
          </AuthenticatedTemplate>

          <UnauthenticatedTemplate>
            <div className="blankslate">
              <h3 className="blankslate-heading">Please sign-in to Azure to add a virtual network</h3>
              <p>
                GitHub currently only supports Azure Virtual Networks. Once you have your virtual network configured,
                you can install the GitHub Virtual Network app in the Azure Marketplace to connect it to your GitHub
                account.
              </p>
              <div className="blankslate-action">
                <SignInButton onError={setError} />
              </div>
            </div>
          </UnauthenticatedTemplate>
        </div>
      </BaseStyles>
    </MsalProvider>
  )
}

try{ NewPage.displayName ||= 'NewPage' } catch {}