export async function callMsGraphSubscriptions(accessToken: string) {
  const headers = new Headers()
  // eslint-disable-next-line i18n-text/no-en
  const bearer = `Bearer ${accessToken}`

  headers.append('Authorization', bearer)

  const options = {
    method: 'GET',
    headers,
  }

  let response
  try {
    response = await fetch('https://management.azure.com/subscriptions?api-version=2020-01-01', options)
  } catch (e) {
    // eslint-disable-next-line no-console
    console.log('Error occurred!', e)
    throw e
  }

  return response.json()
}

