import {useMsal} from '@azure/msal-react'
import {loginRequest} from '../auth-config'
import {Button} from '@primer/react'
import type {ISignInButtonProps} from '../types/virtual-network-settings-types'

export const SignInButton = (props: ISignInButtonProps) => {
  const {instance} = useMsal()
  const {onError} = props

  const handleLogin = () => {
    try {
      instance.loginPopup(loginRequest)
    } catch (e) {
      // @ts-expect-error catch blocks are bound to `unknown` so we need to validate the type before using it
      if (e.errorCode === 'popup_window_error') {
        onError(new Error('Popup window was blocked. Please allow popups for this site and try again.'))
      } else {
        // @ts-expect-error catch blocks are bound to `unknown` so we need to validate the type before using it
        onError(e)
      }

      return
    }
  }
  return (
    <Button variant="primary" onClick={handleLogin} sx={{display: 'inline'}}>
      Sign in
    </Button>
  )
}

try{ SignInButton.displayName ||= 'SignInButton' } catch {}