import React, {useState, useEffect, useId, type ChangeEventHandler} from 'react'
import {callMsGraphSubscriptions} from '../graph'
import type {
  IAzureSubscription,
  ISubscriptionPickerProps,
  ISubscriptionProps,
} from '../types/virtual-network-settings-types'
import {FormControl, Autocomplete} from '@primer/react'

export function SubscriptionPicker(props: ISubscriptionPickerProps) {
  const {onError, appState, setAppState} = props
  const {accessToken, subscriptionError} = appState
  const [subscriptionsLoaded, setSubscriptionsLoaded] = useState(false)
  const [subscriptions, setSubscriptions] = useState<ISubscriptionProps[]>([])
  const [currentSubscription, setCurrentSubscription] = useState<string>('')
  const labelId = useId()

  useEffect(() => {
    const updateSubscriptions = async () => {
      if (!accessToken) {
        return
      }

      let response
      try {
        response = await callMsGraphSubscriptions(accessToken)
      } catch (e) {
        // @ts-expect-error catch blocks are bound to `unknown` so we need to validate the type before using it
        onError(e)
        return
      }

      const subs = response.value.map(({subscriptionId, displayName}: IAzureSubscription) => {
        return {
          id: subscriptionId,
          name: displayName,
        }
      })
      setSubscriptions(subs)
      setAppState({...appState, selectedSubscription: subs[0]})
      setCurrentSubscription(`${subs[0].name} (${subs[0].id})`)
      setSubscriptionsLoaded(true)
    }

    try {
      updateSubscriptions()
    } catch (e) {
      // @ts-expect-error catch blocks are bound to `unknown` so we need to validate the type before using it
      onError(e)
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [accessToken])

  const selectionChanged = (items: AutocompleteItem | AutocompleteItem[]) => {
    if (!Array.isArray(items)) {
      return
    }

    const value = items[0]!
    const sub = subscriptions.find(n => n.id === value.id)

    if (sub) {
      setCurrentSubscription(`${sub.name} (${sub.id})`)
      setAppState({...appState, selectedSubscription: sub})
    } else {
      throw new Error(`Error locating subscription with id ${value.id}`)
    }
  }

  const options = subscriptions.map(({id, name}): AutocompleteItem => {
    return {text: `${name} (${id})`, id}
  })

  const [filterVal, setFilterVal] = React.useState('')
  const handleChange: ChangeEventHandler<HTMLInputElement> = event => {
    setFilterVal(event.currentTarget.value)
  }
  const customFilterFn = (item: AutocompleteItem) => item.text.includes(filterVal)
  interface AutocompleteItem {
    text: string
    id: string
  }

  return (
    <FormControl required sx={{mt: 3}}>
      <FormControl.Label id={labelId}>Susbscription</FormControl.Label>
      <Autocomplete>
        <Autocomplete.Input
          block
          onChange={handleChange}
          value={currentSubscription}
          placeholder="Select a Subscription"
          aria-invalid={!!subscriptionError}
        />
        <Autocomplete.Overlay width="xlarge">
          <Autocomplete.Menu
            aria-labelledby={labelId}
            loading={!subscriptionsLoaded}
            items={options}
            selectedItemIds={[]}
            filterFn={customFilterFn}
            onSelectedChange={selectionChanged}
            selectionVariant="single"
          />
        </Autocomplete.Overlay>
      </Autocomplete>
      {!!subscriptionError && <FormControl.Validation variant="error">{subscriptionError}</FormControl.Validation>}
    </FormControl>
  )
}

try{ SubscriptionPicker.displayName ||= 'SubscriptionPicker' } catch {}