import {useState, useEffect, type ChangeEventHandler} from 'react'
import {callMsGraphVirtualNetworks} from '../graph'
import {FormControl, Select} from '@primer/react'
import type {
  IVirtualNetworkPickerProps,
  IAzureVirtualNetwork,
  IAzureVirtualNetworkSubnet,
  IVirtualNetworkProps,
} from '../types/virtual-network-settings-types'

export function VirtualNetworkPicker(props: IVirtualNetworkPickerProps) {
  const {appState, setAppState, onError} = props
  const {accessToken, selectedResourceGroup, virtualNetworkError} = appState
  const [virtualNetworks, setVirtualNetworks] = useState<IVirtualNetworkProps[]>([])

  useEffect(() => {
    const updateVirtualNetworks = async () => {
      if (!accessToken) {
        return
      }

      if (selectedResourceGroup) {
        const response = await callMsGraphVirtualNetworks(accessToken, selectedResourceGroup)

        const nets = response.value.map((vNet: IAzureVirtualNetwork) => {
          const subnets = vNet.properties.subnets.map((subnet: IAzureVirtualNetworkSubnet) => {
            return {
              id: subnet.id,
              name: subnet.name,
            }
          })

          return {
            id: vNet.id,
            name: vNet.name,
            subnets,
          }
        })
        setVirtualNetworks(nets)

        if (nets.length > 0) {
          let selectedSubnet = null
          if (nets[0].subnets.length > 0) {
            selectedSubnet = nets[0].subnets[0]
          }
          setAppState({...appState, selectedVirtualNetwork: nets[0], subnets: nets[0].subnets, selectedSubnet})
        } else {
          setAppState({...appState, selectedVirtualNetwork: null, subnets: [], selectedSubnet: null})
        }
      } else {
        setVirtualNetworks([])
        setAppState({...appState, selectedVirtualNetwork: null, subnets: [], selectedSubnet: null})
      }
    }

    try {
      updateVirtualNetworks()
    } catch (e) {
      // @ts-expect-error catch blocks are bound to `unknown` so we need to validate the type before using it
      onError(e)
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [accessToken, selectedResourceGroup])

  const handleChange: ChangeEventHandler<HTMLSelectElement> = e => {
    const value = e.target.value
    const vNet = virtualNetworks.find(n => n.id === value)

    if (vNet) {
      let selectedSubnet = null
      if (vNet.subnets.length > 0) {
        selectedSubnet = vNet.subnets[0]!
      }
      setAppState({...appState, selectedVirtualNetwork: vNet, subnets: vNet.subnets, selectedSubnet})
    } else {
      throw new Error(`Error locating vNet with id ${value}`)
    }
  }

  const options = virtualNetworks.map(({id, name}) => {
    return (
      <Select.Option key={id} value={id}>
        {name}
      </Select.Option>
    )
  })

  return (
    <FormControl required sx={{mt: 3}}>
      <FormControl.Label>Virtual Network</FormControl.Label>
      <Select block={true} onChange={handleChange} aria-invalid={!!virtualNetworkError}>
        {options}
      </Select>
      {!!virtualNetworkError && <FormControl.Validation variant="error">{virtualNetworkError}</FormControl.Validation>}
    </FormControl>
  )
}

try{ VirtualNetworkPicker.displayName ||= 'VirtualNetworkPicker' } catch {}