import type React from 'react'
import {useEffect} from 'react'
import {loginRequest} from '../auth-config'
import {useMsal} from '@azure/msal-react'
import {InteractionRequiredAuthError} from '@azure/msal-browser'
import {SubscriptionPicker} from './SubscriptionPicker'
import {ResourceGroupPicker} from './ResourceGroupPicker'
import {VirtualNetworkPicker} from './VirtualNetworkPicker'
import {SubnetPicker} from './SubnetPicker'
import {Button} from '@primer/react'
import type {FormErrors, IRegisterVNetFormProps} from '../types/virtual-network-settings-types'

export function RegisterVNetForm(props: IRegisterVNetFormProps) {
  const {instance, accounts} = useMsal()
  const {appState, setAppState, businessName, createNetworkRegistration, onError} = props

  useEffect(() => {
    const getAccessToken = async () => {
      let response

      try {
        response = await instance.acquireTokenSilent({
          ...loginRequest,
          account: accounts[0],
        })
      } catch (e) {
        if (e instanceof InteractionRequiredAuthError) {
          // fallback to interaction when silent call fails
          try {
            response = await instance.acquireTokenPopup(loginRequest)
          } catch (err) {
            // @ts-expect-error catch blocks are bound to `unknown` so we need to validate the type before using it
            if (err.errorCode === 'popup_window_error') {
              onError(new Error('Popup window was blocked. Please allow popups for this site and try again.'))
            } else {
              // @ts-expect-error catch blocks are bound to `unknown` so we need to validate the type before using it
              onError(err)
            }

            return
          }
        }
        // @ts-expect-error catch blocks are bound to `unknown` so we need to validate the type before using it
        onError(e)
        return
      }
      setAppState({...appState, accessToken: response.accessToken})
    }

    try {
      getAccessToken()
    } catch (e) {
      // @ts-expect-error catch blocks are bound to `unknown` so we need to validate the type before using it
      onError(e)
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [])

  const onSubmit = async (e: React.SyntheticEvent) => {
    e.preventDefault()

    const errors: FormErrors = {
      subscriptionError: null,
      resourceGroupError: null,
      virtualNetworkError: null,
      subnetError: null,
    }

    if (!appState.selectedSubscription) {
      errors.subscriptionError = 'Subscription is required'
    }
    if (!appState.selectedResourceGroup) {
      errors.resourceGroupError = 'Resource group is required'
    }
    if (!appState.selectedVirtualNetwork) {
      errors.virtualNetworkError = 'Virtual network is required'
    }
    if (!appState.selectedSubnet) {
      errors.subnetError = 'Subnet is required'
    }

    setAppState({...appState, ...errors})

    if (
      appState.selectedSubscription &&
      appState.selectedVirtualNetwork &&
      appState.selectedResourceGroup &&
      appState.selectedSubnet
    ) {
      createNetworkRegistration(appState.selectedSubscription, appState.selectedVirtualNetwork, appState.selectedSubnet)
    }
  }

  return (
    <form noValidate onSubmit={onSubmit}>
      <SubscriptionPicker appState={appState} setAppState={setAppState} onError={onError} />
      <ResourceGroupPicker appState={appState} setAppState={setAppState} onError={onError} />
      <VirtualNetworkPicker appState={appState} setAppState={setAppState} onError={onError} />
      <SubnetPicker appState={appState} setAppState={setAppState} />
      <div className="mt-3">
        <Button variant="primary">Add to {businessName}</Button>
      </div>
    </form>
  )
}

try{ RegisterVNetForm.displayName ||= 'RegisterVNetForm' } catch {}