import {NewPage} from './routes/NewPage'
import {registerReactAppFactory} from '@github-ui/react-core/register-app'
import {jsonRoute} from '@github-ui/react-core/json-route'

registerReactAppFactory('virtual-network-settings', () => ({
  routes: [
    jsonRoute({path: '/enterprises/:business/settings/virtual_networks/new', Component: NewPage}),
    jsonRoute({path: '/enterprises/azure_callback', Component: NewPage}),
  ],
}))
