import type {ChangeEventHandler} from 'react'
import {FormControl, Select} from '@primer/react'
import type {ISubnetPickerProps} from '../types/virtual-network-settings-types'

export function SubnetPicker(props: ISubnetPickerProps) {
  const {appState, setAppState} = props
  const {subnets, subnetError} = appState

  const handleChange: ChangeEventHandler<HTMLSelectElement> = e => {
    const value = e.target.value
    const subnet = subnets.find(n => n.id === value)

    if (subnet) {
      setAppState({...appState, selectedSubnet: subnet})
    } else {
      throw new Error(`Error locating subnet with id ${value}`)
    }
  }

  const options = subnets.map(({id, name}) => {
    return (
      <Select.Option key={id} value={id}>
        {name}
      </Select.Option>
    )
  })

  return (
    <FormControl required sx={{mt: 3}}>
      <FormControl.Label>Subnet</FormControl.Label>
      <Select block={true} onChange={handleChange} aria-invalid={!!subnetError}>
        {options}
      </Select>
      {!!subnetError && <FormControl.Validation variant="error">{subnetError}</FormControl.Validation>}
    </FormControl>
  )
}

try{ SubnetPicker.displayName ||= 'SubnetPicker' } catch {}