import {useState, useEffect, type ChangeEventHandler} from 'react'
import {FormControl, Select} from '@primer/react'
import {callMsGraphResourceGroups} from '../graph'
import type {IResourceGroupPickerProps} from '../types/virtual-network-settings-types'
interface IResourceGroupProps {
  readonly id: string
  readonly name: string
}

export function ResourceGroupPicker(props: IResourceGroupPickerProps) {
  const {appState, setAppState, onError} = props
  const {accessToken, selectedSubscription, resourceGroupError} = appState
  const [resourceGroups, setResourceGroups] = useState([])

  useEffect(() => {
    const updateResourceGroups = async () => {
      if (!accessToken) {
        return
      }

      if (selectedSubscription) {
        const response = await callMsGraphResourceGroups(accessToken, selectedSubscription.id)

        setResourceGroups(
          response.value.map(({id, name}: IResourceGroupProps) => {
            return {
              id,
              name,
            }
          }),
        )

        if (response.value.length > 0) {
          setAppState({...appState, selectedResourceGroup: response.value[0].id})
        } else {
          setAppState({...appState, selectedResourceGroup: null})
        }
      } else {
        setAppState({...appState, selectedResourceGroup: null})
      }
    }

    try {
      updateResourceGroups()
    } catch (e) {
      // @ts-expect-error catch blocks are bound to `unknown` so we need to validate the type before using it
      onError(e)
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [accessToken, selectedSubscription])

  const handleChange: ChangeEventHandler<HTMLSelectElement> = e => {
    setAppState({...appState, selectedResourceGroup: e.target.value})
  }

  const options = resourceGroups.map(({id, name}) => {
    return (
      <Select.Option key={id} value={id}>
        {name}
      </Select.Option>
    )
  })

  return (
    <FormControl required sx={{mt: 3}}>
      <FormControl.Label>Resource Group</FormControl.Label>
      <Select block={true} onChange={handleChange} aria-invalid={!!resourceGroupError}>
        {options}
      </Select>
      {!!resourceGroupError && <FormControl.Validation variant="error">{resourceGroupError}</FormControl.Validation>}
    </FormControl>
  )
}

try{ ResourceGroupPicker.displayName ||= 'ResourceGroupPicker' } catch {}